//
// helpers.analytics
//
'use strict';

import ga from 'gua-browserify';

class AnalyticsHelper {
  init(data) {
    this.tracker = ga(data);
  }

  trackEvent(action, label, value) {
    if (action === 'gameNext') {
      this.tracker('send', 'event', 'bingoEvents', action);
    } else {
      if (label === 'amount') {
        this.tracker('send', 'event', 'bingoEvents', action, label, value);
      } else {
        this.tracker('send', 'event', 'bingoEvents', action, label);
      }
    }
  }
}

export default new AnalyticsHelper();
